/* pc, mobile */
#document {
  overflow: hidden;
}

.hidden-p {
  display: none !important;
}

@media (max-width: 751px) {
  .hidden-p {
    display: block !important;
  }
}

.hidden-m {
  display: block !important;
}

@media (max-width: 751px) {
  .hidden-m {
    display: none !important;
  }
}

/* components layout start */
.l-box--faq {
  display: flex;
  margin: 0 14px 50px;
  padding: 0;
  border-radius: 0;
}

.l-list--notice {
  padding: 30px 28px;
  background-color: #f5f5f5;
}

.l-form .c-form--undefined {
  margin: 40px 20px 0;
}

.l-carousel {
  position: relative;
}

.l-carousel__mask {
  position: absolute;
  bottom: 0;
  z-index: 10;
  width: 100%;
}

.l-carousel__mask img {
  width: 100%;
}

.l-carousel--mask {
  clip-path: url(#myClip);
}

.l-error {
  position: relative;
  max-width: 720px;
  height: 100vh;
  margin: 0 auto;
  overflow-x: hidden;
}

.l-gnb {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  width: 100%;
  height: 100%;
}
